<template>
  <v-app>
    <div style="max-height:184px">
      <v-toolbar width="100%" height="120px" flat v-if="wep.wep">
        <v-spacer></v-spacer>
        <v-btn text class="font-weight-bold" @click="childLogin">{{login_info.l_title}}</v-btn>
        <v-spacer></v-spacer>
        <a href="/">
            <img src="./images/logo.png" width="200">
        </a>
        <v-spacer></v-spacer>
        <v-btn text class="font-weight-bold" @click="childJoin" >{{login_info.r_title}}</v-btn>
        <v-spacer></v-spacer>
      </v-toolbar>
      <v-toolbar style="border-bottom: 8px solid #E3DFDE; border-top: 1px solid #E3DFDE;" flat v-if="wep.wep">
        <v-tabs show-arrows grow centered>
          <v-tab v-for="bar in title" :key="bar.title" :to="bar.to" class="font-weight-bold">{{ bar.titleName }}</v-tab>
        </v-tabs>
      </v-toolbar>
    </div>
    <v-navigation-drawer v-model="mobile_header" app clipped v-if="wep.mobaile">
      <v-list dense>
        <v-list-item link @click="childLogin">
          <v-list-item-content>
            <v-list-item-title class="font-weight-bold">{{login_info.l_title}}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
      <v-list dense>
        <v-list-item link @click="childJoin">
          <v-list-item-content>
            <v-list-item-title class="font-weight-bold">{{login_info.r_title}}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
      <v-list dense v-for="bar in title" :key="bar.title">
        <v-list-item link :to="bar.to">
          <v-list-item-content>
            <v-list-item-title class="font-weight-bold">{{bar.titleName}}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-app-bar app color="white" clipped-left :height="mb_header_height" v-if="wep.mobaile && mhv">
      <v-app-bar-nav-icon @click.stop="mobile_header = !mobile_header"></v-app-bar-nav-icon>
      <v-spacer></v-spacer>
      <img src="./images/logo.png" height="100%" >
      <v-spacer></v-spacer>
      <v-btn icon @click="childJoin">
        <v-icon>mdi-account</v-icon>
      </v-btn>
    </v-app-bar>
    <v-content 
    :style="wep.wep ? wep_content : mobaile_content">
          <router-view :key="$route.fullPath" ></router-view>
    </v-content>
    <LoginForm ref="login_form" v-bind:login_info="login_info" @child="change"/>
    <JoinForm ref="join_form" v-bind:login_info="login_info"/>
  </v-app>
</template>

<script>
import LoginForm from './components/LoginForm'
import JoinForm from './components/JoinForm'
  export default {
    data: () => ({
      title: [
        { titleName: 'HOME',  to: '/'}, 
        { titleName: 'ABOUT', to: '/about'},
        { titleName: 'EVENT', to: '/event'},
        { titleName: 'EDUCATION', to: '/education' },
        { titleName: 'ABANDONED DOG', to: '/abandonedDog' },
        { titleName: 'ADINPAGE', to: '/adminPage' },
      ],
      xs_b : false,
      mobile_header: false,
      el : null,
      login_dialog : false,
      login_info : {},
      drawer: null,
      point : {},
      wep_content : "padding:0px; border-top: 1px solid #E3DFDE;" ,
      mobaile_content : "padding-top:100px;",
      mb_header_height : "100px",
      mhv : true,
    }),
    components: {
      LoginForm,
      JoinForm,
    },
    mounted(){
      // 사용자 로그인 여부를 확인하여 LOGIN/LOGOUT, JOIN/MYPAGE 판별 
      this.loginCheck();
      this.mbHeaderControl();
      
    },
    methods :{
      change(data){
          this.login_info = data;
      },
      ppp(){
          console.log(this.$vuetify.breakpoint.xs);
      },
      childLogin(){
        this.$refs["login_form"].inOrOut_button()
      },
      childJoin(){
        this.$refs["join_form"].joinOrMypageButton()
      },
      mbHeaderControl(){
        const headerHeight = this.headerHeight;
        let y = window.scrollY;
        window.addEventListener('scroll', (delta) => {
          let _y = window.scrollY, bl;
          if(_y < 100){
            bl = true;
          }else if(_y < y){
            bl = true;
          }else{
            bl = false;
          }
          y = _y; 
          headerHeight(bl);
        })
      },
      headerHeight(bl){
        this.mhv = bl;
      },
      loginCheck(){
        this.$http.get('/api/login_spv').then((res) => {
          if(res.data.login_code === 0 || res.data.login_code === undefined) {
              this.login_info = {
                  number: 0,
                  l_title: "LOGIN",
                  r_title: "JOIN"
              };
          }else{ 
              this.login_info = {
                  number: res.data.login_code,
                  l_title: "LOGOUT",
                  r_title: "MYPAGE"
              };
          };
        }).catch(err => {
            alert(arr);
        });
      }
    },
    computed : {
      wep(){
        return this.$vuetify.breakpoint.xs || this.$vuetify.breakpoint.sm ? {wep : false, mobaile : true} : {wep : true, mobaile : false};
      },
    },
  }
</script>

