<template>
    <v-card tile 
        class="card1_top d-flex align-content-center flex-wrap" 
        min-width="100%"
        :min-height="windowSize.y"
        :width="windowSize.x"
        :height="lg_xl_ok ? windowLgSize : 'auto'"
            color="#FFF8DC">
            <v-layout style="padding-top:2%">
                <v-flex xs0 lg2></v-flex>
                <v-flex xs12 lg8>
                    <v-card-title :style="title">반려견 행사</v-card-title>
                    <v-card-subtitle :style="subTitle">반려견을 위한 행사를 공유해보세요.</v-card-subtitle>
                </v-flex>
                <v-flex xs0 lg2></v-flex>
            </v-layout>
            <div class="d-flex justify-center" style="width:100%">
                <div class="d-flex justify-center" style="width:90%">
                    <v-layout wrap style="padding-bottom:2%">
                        <v-flex xs0  lg2 xl2  style="padding:0px;"></v-flex>
                        <v-flex xs6 sm6 md3 lg2 xl2  style="padding:0px;"
                            v-for="bar in poster_item" :key="bar.id"> 
                            <v-col cols="12">
                                <v-hover v-slot:default="{ hover }">
                                        <v-img :elevation="hover ? 16 : 2" :src="bar.img" style="width:100%;">
                                        <v-expand-transition>
                                        <div
                                            v-if="hover"
                                            class="d-flex darken-2 v-card--reveal white--text"
                                            style="height: 100%;"
                                        >
                                            <div class="poster_op"></div>
                                            <v-btn @click="goEvent">바로가기</v-btn>
                                        </div>
                                        </v-expand-transition>
                                    </v-img>
                                </v-hover>
                            </v-col>
                        </v-flex>
                        <v-flex xs0 lg2 xl2 style="padding:0px;"></v-flex>
                    </v-layout>
                </div>
            </div>
        </v-card>
</template>

<script>
export default {
    props:['windowSize', 'lg_xl_ok', 'windowLgSize'],
    data:() => ({
        poster_item : [
            { id : 'poster1' , img : require('../../images/testPoster.jpg')},
            { id : 'poster2' , img : require('../../images/testPoster.jpg')},
            { id : 'poster3' , img : require('../../images/testPoster.jpg')},
            { id : 'poster4' , img : require('../../images/testPoster.jpg')},
        ],
    }),
    computed :{
        title(){
            return this.lg_xl_ok === 'ok' ? {'font-size' : '40px', "padding-bottom" : "3%"} 
            : {'font-size' : '20px', "padding-bottom" : "2%"}
        },
        subTitle(){
            return this.lg_xl_ok === 'ok' ? { 'font-size' : '20px'} : { 'font-size' : '15px'}
        }
    },
    methods :{
        goEvent(event){
        const id = event.target
        },
    }
}
</script>

