<template> 
    <!-- 브라우저 크기(breakpoint)가 lg이상일 경우 -->
    <v-container fluid v-resize="onResize" style="padding:0px; solid #E3DFDE;" v-if="lg_xl_ok"> 
        <v-item-group v-model="window" class="shrink"
            mandatory tag="v-flex" id="window_wrap">
            <div id="item_groups">
                <v-item  v-for="n in length" :key="n" 
                v-slot:default="{ active, toggle }" ref="button">
                    <div>
                        <v-btn :input-value="active" icon @click="toggle">
                            <v-icon>mdi-record</v-icon>
                        </v-btn>
                    </div>
                </v-item>
            </div>
            <v-window v-model="window" class="elevation-1" vertical tile>
                <v-window-item >
                    <HomeFirst 
                        v-bind:windowSize="windowSize" 
                        v-bind:windowLgSize="windowLgSize"
                        v-bind:lg_xl_ok="lg_xl_ok"
                        v-bind:button_size="buttonSize"
                        @child="windowPageMove"
                    ></HomeFirst>
                </v-window-item>
                <v-window-item>
                    <v-card>
                    <EventCard 
                        v-bind:windowSize="windowSize"
                        v-bind:windowLgSize="windowLgSize"
                        v-bind:lg_xl_ok="lg_xl_ok"
                    ></EventCard>
                    </v-card>
                </v-window-item>
                <v-window-item>
                    <EducationCard 
                    v-bind:windowSize="windowSize"
                    v-bind:windowLgSize="windowLgSize"
                    v-bind:lg_xl_ok="lg_xl_ok"
                    v-bind:button_size="buttonSize">
                    </EducationCard>
                </v-window-item>
                <v-window-item>
                    <Abandoned 
                    v-bind:windowSize="windowSize"
                    v-bind:windowLgSize="windowLgSize"
                    v-bind:lg_xl_ok="lg_xl_ok"
                    ></Abandoned>
                </v-window-item>
            </v-window>
        </v-item-group>
    </v-container>
    <!-- 브라우저 크기(breakpoint)가 md 이하일 경우 -->
    <v-container fluid v-resize="onResize" style="padding:0px; solid #E3DFDE;" v-else>
        <HomeFirst 
            v-bind:windowSize="windowSize" 
            v-bind:button_size="buttonSize"
            @child="windowPageMove"
        ></HomeFirst>
        <EventCard 
            v-bind:windowSize="windowSize"
            v-bind:windowLgSize="windowLgSize"
            v-bind:lg_xl_ok="lg_xl_ok"
        ></EventCard>
        <EducationCard 
            v-bind:windowSize="windowSize"
            v-bind:windowLgSize="windowLgSize"
            v-bind:lg_xl_ok="lg_xl_ok"
            v-bind:button_size="buttonSize">
        </EducationCard>
        <Abandoned v-bind:windowSize="windowSize"></Abandoned>
    </v-container>

</template>
<script>

</script>
<script>
import HomeFirst from "../components/home/HomeFirst";
import EventCard from "../components/home/Event";
import EducationCard from "../components/home/Education";
import Abandoned from "../components/home/Abandoned";

export default {
    // props:['wep'],
    data: () => ({
        button_size : { //레이아웃 마다 버튼 사이즈
            xs : "100",
            sm : "120",
            md : "150",
            lg : "170",
            xl : "200",
        },
        windowSize: { //브라우저 크기에 따라 컴포넌트의 크기 값
            x: 0,
            y: 0,
        },
        windowLgSize: 0, //breakpoint가 lg 이상일 경우 컴포넌트의 높이 값
        window : 0, // vuetify에서 제공하는 window기능을 이용하기 위한 필수 값
        length : 4, // window 태그의 length
        active_number : 0, // window 태그의 현재 인덱스 번호
        active_bl : true, // 마우스 휠 감지의 중복을 방지하기위한 boolean 변수
    }),
    methods:{
        onResize(){ //브라우저의 크기가 변경됐을 경우 컴포넌트들의 사이즈 변경
            this.windowSize = { x: window.innerWidth, y: window.innerHeight * 0.6} //md 이하
            this.windowLgSize = window.innerHeight - 185; //lg 이상
        },
        windowPageMove(num){ //lg, xl 사이즈 일때 홈의 맨위에 있는 버튼들을 누르면 해당 윈도우로 이동 
            this.$refs["button"][num]._events.change[0]();
            this.active_number = num;
            setTimeout(() => { this.active_bl = true; }, 250);
        },
        mousewheelEvent(){
            window.addEventListener('mousewheel', (delta) => {
               if(this.active_bl && this.$route.name === "home" && this.lg_xl_ok){
                    this.active_bl = false;
                    if(delta.wheelDelta > 0 && this.active_number > 0){
                        this.active_number --;
                    }else if(delta.wheelDelta < 0 && this.active_number < 3){
                        this.active_number ++;
                    }
                    this.windowPageMove(this.active_number);
               }
            })    
        },

    },
    mounted(){
        this.onResize(); 
        this.mousewheelEvent();
    },
    computed:{
        buttonSize(){ //버튼들 사이즈 %로 하면 레이아웃마다 원하는 사이즈가 다르기 때문에 breakpoint가 변경될 때 마다 변경
            const key = this.$vuetify.breakpoint.name;
            return this.button_size[key];
        },
        lg_xl_ok(){ //모든 컴포넌트들이 사용하는 breakpoint가 lg 이상일 경우 true 미만 일경우 false
            return this.$vuetify.breakpoint.lg || this.$vuetify.breakpoint.xl ? true : false;
        },
    },
    components :{
        HomeFirst,
        EventCard,
        EducationCard,
        Abandoned,
    },
    
}
</script>

<style >
#home_card div{
    margin: 2%;
}
#home_card button{
    border: 0;
}
#event_title h1{
    font-size : 3em
}
#event_title p{
    font-size : 2em
}
.v-card--reveal {
  align-items: center;
  bottom: 0;
  justify-content: center;
  position: absolute;
  width: 100%;
}
.poster_op{
    width:100%;
    height: 100%;
    position: absolute;
    opacity: 0.7;
    background-color: black;
}
#item_groups{
    position : absolute;
    left:97%;
    top:43%;
    z-index: 2;
}

</style>