<template>
    <div id="pdf">
        <div ref="poster" id="poster" :style="poster_style.p">
            <v-img :src="main_img_url" id="main_img" class="d-flex justify-center">
                <div :style="poster_style.b" class="text-center">전단지를 때지 말아주세요. 반려동물을 찾으면, 반드시 수거해 가겠습니다. 부탁드립니다.</div>
                    <div id="dog_name" class="d-flex align-center">
                    <div :style="poster_style.n" class="text-center">{{form.ad_name}}</div>
                </div>
            </v-img>
            <div id="title_area">
                <div id="title_div" class="d-flex align-center">
                    <div class="text-center font-weight-bold" :style="poster_style.t">
                    {{form.ad_title}}
                    </div>
                </div>
            </div>
            <div v-if="ad_honorarium.bl" class="text-center font-weight-bold" :style="poster_style.h">
                사례금 : {{ad_honorarium.hr}}만원
            </div>
            <div id="ad_info_area" class="d-flex align-center font-weight-bold">
                <div id="ad_ingo_div">
                    <div :style="poster_style.d" class="d-flex justify-center text-center">
                        <v-spacer></v-spacer>
                        연락처 : {{form.ad_pone}}
                        <v-spacer></v-spacer>
                        날짜 : {{form.ad_date}}
                        <v-spacer></v-spacer>
                    </div>
                    <div :style="poster_style.d">
                    {{form.ad_place}}
                    </div>
                </div>
            </div>
            <!-- 124자 이네로 -->
            <div :style="poster_style.c" class="d-flex justify-center text-center">
                <div style="width:90%;">
                {{form.ad_detail}}
                </div>
            </div>
            <v-divider class="mt-2"></v-divider>
            <div id="sb_img_area" class="d-flex justify-center text-center">
                <div id="sb_img_div" v-for="(bar, index) in sb_img_urls" :key="index" >
                    <img  style="width:100%;height:100%;" :src="bar" >
                </div>
            </div>
        </div>
        <v-dialog  v-model="loader_dialog" hide-overlay  persistent width="350">
            <v-card color="primary" dark>
                <v-card-text>PDF파일을 생성 중입니다. 잠시만 기다려 주세요.
                    <v-progress-linear  indeterminate color="white"  class="mb-0"></v-progress-linear>
                </v-card-text>
            </v-card> 
        </v-dialog>
    </div>
</template>


<script>
export default {
    props:['form', 'ad_honorarium', 'main_img_url', 'sb_img_urls', 'first'],
    data: () => ({
        poster_style : {
            p:'width:420px; height:594px;', b:'color:white; font-size:0.66em; z-index:10;',
            n:'width:100%; color:white; font-size:2em;', t:'width:100%; font-size:2em; color:red;', h:'font-size:1.25em',
            d:'width:80%; margin:0 auto; font-size:1em;', c:'width:100%; height:13%; font-size:0.8em;',
        } ,
        loader_dialog : false,
        testIMG: null,
    }),
    methods:{
        createPDF(){
            this.loader_dialog = true;
            const el = document.getElementById('poster');
            let location = this.getElLocation(el);
            // var sp = window.scrollY || document.documentElement.scrollTop;
            location.y = this.first ? location.y : location.y + (window.scrollY || document.documentElement.scrollTop);
            var pdf =  html2canvas(el,{
            x: location.x, y : location.y, scale: 4}
            ).then(function(canvas) {
                var doc = new jsPDF({
                'orientation': 'p',
                'unit': 'mm',
                'format': 'a4'
                });
                const imgData = canvas.toDataURL('image/png'),
                imgWidth = 210,
                pageHeight = imgWidth * 1.414,
                imgHeight = canvas.height * imgWidth / canvas.width,
                position = 0;
                doc.addImage(imgData.toString(), 'PNG', 0, position, imgWidth, imgHeight);
                doc.save('실종 반려견.pdf');
                return false;
            })
            pdf.then(value => {
                this.loader_dialog = value;
                this.$emit("child",1);
            });
        },
        getElLocation(el) {
            let x = 0;
            let y = 0;
            if(el.offsetParent) {
                do {
                    x += el.offsetLeft;
                    y += el.offsetTop - (el.scrollTop !== 0 ? el.scrollTop : 0);
                } while (el = el.offsetParent);
            }
            return {x : x, y : y};
        },
    },
}

</script>

<style scoped>
#main_img{
width:100%; 
height:31%; 
background-color:#E3DFDE;
position:relative;
}
#dog_name{
width:100%;
height:100%;
z-index:10;
position:relative;
}
#main_img_btn{
position:absolute; z-index:1; top:50%; left:50%; transform:translate(-50%, -50%);
}
#title_area{
width:100%; height:16%; 
}
#title_div{
width:80%; height:100%; margin:0 auto;
}
#ad_info_area{
height:14%;
}
#ad_info_div{
width:100%;
}
#sb_img_area{
width:100%; height:22%; position:relative;
}
#sb_img_div{
width:27%; margin:3%; height:65%; position:relative; background-color:#E3DFDE;
}
.sb_img{
position:absolute; z-index:1; top:50%; left:50%; transform:translate(-50%, -50%);
}
</style>
