<template>
  <v-carousel cycle height="400" hide-delimiter-background show-arrows-on-hover v-resize="onResize">
    <div v-if="set_events">
      <v-carousel-item v-for="(e, i) in set_events" :key="i">
        <v-sheet tile :color="colors[i]" height="100%">
          <v-row class="fill-height" align="center" justify="center">
              <v-layout wrap style="width:100%; max-width:1100px;" class="justify-center">
                <v-col :cols="e.length > 2 ? '4' : e.length > 1 ? '4' : '12'" v-for="(bar, j) in e" :key="j">
                  <div style="width:200px; height:300px; margin:0 auto;">
                    <a @click="parentColl(bar)">
                      <img style="width:100%; height:100%;" :src="`//localhost:3000/api/img/get_img/event/${bar.ev_img}`">
                    </a>
                  </div>
                </v-col>
              </v-layout>
          </v-row>
        </v-sheet>
      </v-carousel-item>
    </div>
    <div v-else>
      <v-carousel-item>
        <v-sheet tile :color="colors[0]" height="100%">
          <v-row class="fill-height" align="center" justify="center">
              <v-layout wrap style="width:100%; max-width:1100px;" class="justify-center">
                <v-card-title>
                  향후 등록된 행사가 없습니다.
                </v-card-title>
              </v-layout>
          </v-row>
        </v-sheet>
      </v-carousel-item>
    </div>
  </v-carousel>
</template>

<script>
  export default {
    data : () => ({
      colors: [
        'indigo',
        'warning',
        'pink darken-2',
        'red lighten-1',
        'deep-purple accent-4',
        'purple darken-1',
        'indigo',
        'warning',
        'pink darken-2',
        'red lighten-1',
        'deep-purple accent-4',
        'purple darken-1',
      ],
      events:null, 
      count:null,
      set_events:null,
    }),
    mounted(){
      this.getTodayEvent();
    },
    methods:{
      //페이지 리사이즈를 감지하고 count변수를 변경하여
      //watch에 지정해 놓은 setCarousel를 호출
      onResize(){
        if(this.events){
          const width = window.innerWidth;
          this.count = width > 699 ? 3 : width > 649 ? 2 : 1;
        }
      },
      //백앤드로 부터 행사 정보를 받아오고 onResize함수를 호출
      getTodayEvent(){
        const today = new Date(), 
        start = `${today.getFullYear()}-${today.getMonth()+1}-${today.getDate()}`;
        let num = 0;
        this.$http.get(`api/event/get_asc/${start}`).then(res => {
          if(res.data.message){
            this.events = res.data.items;
            this.onResize();
          }else{
            this.events = null;
          }
        }).catch(err => {
          alert('행사 정보를 가져오는 과정에서 문제가 발생했습니다.')
        })
      },
      //배너의 carousel의 내부 갯수 총 갯수를 브라우저 크기에 따라 변경
      setCarousel(count){
        if(this.events){
          const events = this.events;
          let parent = new Array(), index = 0;
          for(let i = 0; i < events.length;){
            let child = new Array();
            for(let j = 0; j < count; j++){
              child.push(events[i++]);
              if(i >= events.length){
                break;
              }
            }
            parent.push(child);
          }
          this.set_events = parent;
        }else{
          this.set_event = null;
        }
      },
      //부모 컴포넌트의 getPoster함수를 호출하고
      //getPoster는 함수는 달력 컴포넌트의 showEvent함수를 호출,
      //행사 포스터를 보여줌
      parentColl(item){
        this.$emit('child', item);
      }
    },
    watch:{
      count : function(hook){
        this.setCarousel(hook);
      }
    }
  }
</script>

