<template>
    <v-btn width="100%" height="60px" color="success" dark @click="naverLogin">NAVER 계정으로 로그인
    </v-btn>
</template>

<script>
export default {
    data: () => ({
        client_id : 'y2GchClz01E_mwS85HM7',
        state: "RAMDOM_STATE",
        redirectURI: null,
        api_url: null,
    }),
    mounted(){
        this.redirectURI = encodeURI("http://localhost:3000/api/naver_login/callback/");
        this.api_url = 'https://nid.naver.com/oauth2.0/authorize?response_type=code&client_id=' 
            + this.client_id + '&redirect_uri=' 
            + this.redirectURI + '&state=' + this.state;
    },
    methods:{
        naverLogin(){
            location.href = this.api_url;
        }
    }
}
</script>