<template>
<v-row justify="center">
  <v-dialog v-model="join_dialog" persistent  width="600px">
    <v-card>
      <v-card-title>
        <span class="headline">회원가입</span>
      </v-card-title>
      <v-card-text>
      <v-container>
      <v-row>
        <v-col cols="12">
        <center>
          <img src="../images/logo.png" width="200">
        </center>
          <v-text-field
          ref="member_name"
          v-model="member_name"
          :rules="[() => !!member_name || '이름을 입력해주세요.']"
          :error-messages="errorMessages"
          label="이름"
          clearable
          required
          ></v-text-field>
        </v-col>
        <v-col cols="12" sm="8" xs="12">
              <v-text-field
              ref="member_email"
              v-model="member_email"
              :rules="emailRules"
              :error-messages="errorMessages"
              label="이메일"
              clearable
              required
              @blur="duplicateEmail"
              ></v-text-field>
        </v-col>
        <v-col cols="12" sm="4" xs="12">
              <v-btn block height="50px" class="font-weight-bold"  @click="sendCertifyNumber">인증번호 보내기</v-btn>
        </v-col>
        <v-col cols="12">
              <v-text-field
              ref="certify_number"
              v-model="certify_number"
              :rules="[() => !!certify_number || '인증번호를 입력하세요.']"
              :error-messages="errorMessages"
              :type="showPW ? 'text' : 'password'"
              label="인증번호"
              clearable
              required
              ></v-text-field>
        </v-col>
        <v-col cols="12" sm="6" xs="12">
          <v-text-field
          ref="member_password"
          v-model="member_password"
          :rules="passwordRules"
          :error-messages="errorMessages"
          label="비밀번호"
          clearable
          required
          :type="showPW ? 'text' : 'password'"
          ></v-text-field>
        </v-col>
        <v-col cols="12" sm="6" xs="12">
          <v-text-field
          ref="member_password_check"
          v-model="member_password_check"
          :rules="passwordCheckRules"
          :eroor-messages="errorMessages"
          :type="showPW ? 'text' : 'password'"
          label="비밀번호 확인"
          clearable
          required
          ></v-text-field>
        </v-col>
        <v-btn block height="60px" color="primary" @click="submit">회원가입</v-btn>
      </v-row>
      </v-container>
      </v-card-text>
      <v-card-actions>
        <v-btn color="blue darken-1" class="font-weight-bold" text @click="dataReset">초기화</v-btn>
        <v-spacer/>
        <v-btn color="blue darken-1" class="font-weight-bold" text @click="formClose">닫기</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</v-row>
</template>

<script>
  export default {
    data () {
      return {
        join_dialog: false,
        member_email:null,
        certify_number:null,
        member_password:null, //dataReset 함수 호출시 passwordRules안에서 length를 사용하기 때문에 되긴하지만 type err를 방지
        member_password_check:null,
        member_name:null,
        emailRules: [
          v => !!v || '이메일을 입력해주세요.',
          v => /.+@.+/.test(v) || '이메일 양식에 맞지 않습니다.',
        ],
        passwordRules:[
          v => v === null ? '비밀번호를 입력해주세요.'
          : v.length >= 8 && v.length <=25 || '8~25자의 영문, 숫자, 특수문자를 입력하세요.',
          v => /[0-9]/g.test(v) || '숫자가 포함되어있지 않습니다.',
          v => /[a-z]/ig.test(v) || '영문이 포함되어있지 않습니다.',
          v => /[`~!@@#$%^&*|₩₩₩'₩";:₩/?]/gi.test(v) || '특수문자가 포함되있지 않습니다.',
        ],
        passwordCheckRules:[
          v => !!v || '비밀번호 확인이 입력되어있지 않습니다.',
          v => v === this.member_password || '비밀번호가 일치하지 않습니다.',
        ],
        errorMessages: '',
        formHasErrors: false,
        showPW: false,
      }
    },
    props: ['login_info'],
    mounted(){
      // this.$refs["certify_number"].disabled = false;
    },
    computed:{
      form(){
        return{
          member_name:this.member_name,
          member_email: this.member_email,
          certify_number:this.certify_number,
          member_password:this.member_password,
          member_password_check:this.member_password_check,
        }
      }
    },
    methods:{
      test(){
        //  alert(
        //    "xs : " + this.$vuetify.breakpoint.xs 
        //    + "\nsm : " + this.$vuetify.breakpoint.sm 
        //    + "\nmd : " + this.$vuetify.breakpoint.md
        //    + "\nlg : " + this.$vuetify.breakpoint.lg 
        //    + "\nxl : " + this.$vuetify.breakpoint.xl)
      },
      test2(){
        
      },
      duplicateEmail(){
        let me = this.$refs["member_email"];
        const num = me.errorBucket.length;
        if(num === 0){
          this.$http.get("/api/join/duplicate_email?email=" + this.member_email).then(
            (res) => {
              if(res.data.code !== 0){
                me.errorBucket.push("이미 사용중인 이메일 입니다.")
              }
            },
            (err)=> {
              alert(err);
            }).catch(err => {
              alert(err);
            })
        }
      },
      joinOrMypageButton(){
        if(this.login_info.number !== 0){
          alert("회원가입 페이지로 이동할 예정");
        }else{
          this.join_dialog = true;
        }
      },
      dataReset(){  //입력한 데이터를 초기화 시켜주는 메소드임
        this.errorMessages = [] // 에러 메세지 초기화
        this.formHasErrors = false // 에러 불리언 값 초기화
        Object.keys(this.form).forEach(f => {//폼에 있는 모든 데이터를 초기화 시켜주는 for문
        //reset이라는 함수를 사용하면 되지만 
        // 폼의 데이터를 undefind로 리셋 시켜 에러를 발생시키 때문에 직접 변수 리셋
          this.$refs[f].isResetting = true;
          this.$refs[f].internalValue = Array.isArray(this.internalValue) ? [] : null;
        })
      },
      formClose(){ //폼을 닫을 경우 실행
        this.dataReset(); // 데이터 초기화
        this.join_dialog = false;
      },
      submit () { //회원 가입 요청을 넣는 메소드 
        this.formHasErrors = false
        //현제 이코드는 form데이터가 빈값만 아니면 무조건 콜 하는 부분임
        Object.keys(this.form).forEach(f => {
          // console.log(this.$refs[f])
          if (!this.form[f] || this.$refs[f].hasError) {
            this.formHasErrors = true;
            this.$refs[f].validate(true);  // 폼의 에러부분에 메세지 띄워주는 메소드
          }
        })

        if(!this.formHasErrors){
          const item = {
            'email' : this.member_email,
            'name' : this.member_name,
            'certify_number' : this.certify_number,
            'password' : this.member_password
          }
          this.$http.post("/api/join/input_member", item).then(
            (res) => {
              if(res.data.message === 'success'){
                this.member_password = [];
                this.member_password_check = [];
                alert("회원가입이 완료되었습니다.");
                this.formClose();
              }
            }).catch(err => {
              alert(err);
            })
        }
      },
      sendCertifyNumber(){ //인증번호 전송요청 메소드
        let email_field = this.$refs["member_email"]
        if(!email_field.hasError){ //폼의 이메일 부분에 에러가 없다면
          email_field.disabled = true; //이메일 변경 방지
          const item = {'email' : this.member_email}
          this.$http.post ('api/join/certify-number', item).then(
            (res) => {
              if(res.data.message === 0){
                email_field.disabled = true;
              }
            }).catch(err => {
              alert(err);
              email_field.disabled = false;
            })
        }else{
          email_field.validate(true)
        }
      },

    }
  }

</script>