<template>
  <v-container fluid style="padding:0px; solid #E3DFDE;">
      <EVCarouesl
      @child="getPoster"
      />
      <v-col cols="12">
        <EvCalendar 
        ref="canlender"
        :event_list="event_list"
        :events_info="event_info"/>
      </v-col>
  </v-container>
</template>

<script>
import EvCalendar from '../components/event/EventCalendar';
import EVCarouesl from '../components/event/EventCarouesl';

export default {
  components: {
      EvCalendar,
      EVCarouesl,
  },
  data : () => ({
    event_list : null,
    events_info : null,
  }),
  mounted () {
  },
  methods:{
    getPoster(data){
      this.$refs.canlender.childEvent(data);
    },
  }
}


</script>


