<template>
<v-row justify="center">
  <!-- <v-btn text class="font-weight-bold" @click="inOrOut_button">{{login_info.l_title}}</v-btn> -->
  <v-dialog v-model="login_dialog" persistent width="600px">
    <v-card>
      <v-card-title>
        <span class="headline">로그인</span>
      </v-card-title>
      <br>
      <v-container>
        <v-row>
          <v-col cols="12">
            <center>
              <img src="../images/logo.png" width="200">
            </center>
            <br><br>
            <v-text-field
            ref="user_email"
            v-model="user_email"
            :rules="[() => !!user_email || '이메일을 입력해주세요.']"
            :eroor-messages="errorMessages"
            label="이메일"
            filled clearable
            ></v-text-field>
            <v-text-field
            ref="user_pw"
            v-model="user_pw"
            :rules="[() => !!user_pw || '비밀번호를 입력해주세요.']"
            :eroor-messages="errorMessages"
            :type="showPW ? 'text' : 'password'"
            label="비밀번호"
            filled clearable
            ></v-text-field>
            <span style="color:red; font-size:12px">{{ex_message}}</span>
            <br><br>
            <v-btn block height="60px" color="primary" @click="tryLogin">로그인</v-btn>
            <br>
            <NLB/>
          </v-col>
        </v-row>
      </v-container>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" class="font-weight-bold" text @click="formClose">닫기</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</v-row>
</template>

<script>
import NLB from '../components/NaverLoginButton'
  export default {
    data: () => ({
      login_dialog: false,
      errorMessages: '',
      user_email: null,
      showPW: false,
      user_pw: null,
      ex_message : '',
      formHasErrors: false,
    }),
    props: ['login_info'],
    computed:{
      form(){
        return{
          user_email:this.user_email,
          user_pw:this.user_pw,
        }
      }
    },
    methods :{
      inOrOut_button(){
        if(this.login_info.number !== 0){
          this.$http.get("/api/login_spv/logout").then(
            (res) => {
              this.$emit("child", {
                number: 0,
                l_title: "LOGIN",
                r_title: "JOIN"
              });
            },
          ).catch(err => {
            alert(err)
          });
        }else{
          this.login_dialog = true;
        }
      },
      dataReset(){  //입력한 데이터를 초기화 시켜주는 메소드
        this.errorMessages = [] // 에러 메세지 초기화
        this.formHasErrors = false // 에러 불리언 값 초기화
        //폼에 있는 모든 데이터를 초기화 시켜주는 for문
        Object.keys(this.form).forEach(f => {
          this.$refs[f].reset()
        })
      },
      formClose(){ //폼을 닫을 경우 실행
        this.dataReset(); // 데이터 초기화
        this.login_dialog = false;
      },
      tryLogin(){
        this.formHasErrors = false
        Object.keys(this.form).forEach(f => {
           if (!this.form[f] || this.$refs[f].hasError) {
            this.formHasErrors = true;
            this.$refs[f].validate(true) // 폼의 에러부분에 메세지 띄워주는 메소드
          }
        })
        if(!this.formHasErrors){
          this.$http.post("/api/login", {'email' : this.user_email, 'password' : this.user_pw,}).then(
            (res) => {
              const rs = res.data.message
              if(rs === 0){
                //로그인 성공
                this.$emit("child", {
                  number: 1,
                  l_title: "LOGOUT",
                  r_title: "MYPAGE"
                });
                this.formClose();
              }else{
                this.ex_message = "가입하지 않은 아이디이거나, 잘못된 비밀번호입니다."
              }
            }).catch(err => {
            alert(err);
            })
          }
        }
    },
    mounted () {
    },
    components:{
      NLB,
    }
  }
</script>