import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Event from '../views/Event.vue'
import About from '../views/About.vue'
import Education from '../views/Education.vue'
import AbandonedDog from '../views/AbandonedDog.vue'
import AdminPageTest from '../views/adminPageTest.vue'
import AdminPage from '../views/AdminPage.vue'
import page404 from '../views/404page.vue'
// import AbandonedDogInput from '../views/AbandonedDogInput.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/event',
    name: 'event',
    component: Event
  },
  {
    path: '/about',
    name: 'about',
    component: About
  },
  {
    path: '/education',
    name: 'education',
    component: Education
  },
  {
    path: '/abandonedDog',
    name: 'abandonedDog',
    component: AbandonedDog
  },
  {
    path: '/adminPageTest',
    name: 'adminPageTest',
    component: AdminPageTest
  },
  {
    path: '/adminPage',
    name: 'adminPage',
    component: AdminPage
  },
  {
    path: '*',
    name : 'page404',
    component : page404
  },
  // {
  //   path: '/abandonedDog/input',
  //   name: 'abandonedDogInput',
  //   component: AbandonedDogInput
  // },
  // {
  //   path: '/callback/*',
  //   name: 'Home',
  //   component: Home,
  // }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
