<template>
    <v-col cols="12" style="padding:0px;">
        <!-- :height="xs_ok==='ok'||sm_md_ok==='ok'? windowSize.y: windowLgSize" -->
        <div id="controll_card"> 
            <v-img src="../../images/test.jpg" 
                class="align-center" 
                :width="windowSize.x"
                :height="lg_xl_ok ? windowLgSize : 'auto'"
                min-width="100%">
                <!-- 모바일 사이즈일 경우 -->
                <div class="d-flex justify-center mt-12 pt-12" id="home_card" v-if="xs_ok">
                    <div v-for="bar in img_classes_xs_sm1" :key="bar.id" >
                        <v-btn
                        :width="button_size"
                        :height="button_size"
                        outlined fab color="#9ACD32"
                        @click="goFocus">
                            <img :id="bar.id"
                            :src="bar.img"
                            :width="button_size"
                            :height="button_size">
                        </v-btn>
                    </div>
                </div>
                <div class="d-flex justify-center mt-6 pt-6" id="home_card"  v-if="xs_ok">
                    <div v-for="bar in img_classes_xs_sm2" :key="bar.id" >
                        <v-btn
                        :width="button_size"
                        :height="button_size"
                        outlined fab color="#9ACD32"
                        @click="goFocus">
                            <img :id="bar.id"
                            :src="bar.img"
                            :width="button_size"
                            :height="button_size">
                        </v-btn>
                    </div>
                </div>
                <!-- 보편적으로 브라우저 크기를 줄인경우 -->
                <div class="d-flex justify-center mt-12 pt-12 " id="home_card"  v-else>
                    <div v-for="bar in img_classes" :key="bar.id" class="mt-12 pt-12" >
                        <v-btn
                        :width="button_size"
                        :height="button_size"
                        outlined fab color="#9ACD32"
                        @click="goFocus">
                            <img :id="bar.id"
                            :src="bar.img"
                            :width="button_size"
                            :height="button_size">
                        </v-btn>
                    </div>
                </div>
            </v-img>
        </div>
    </v-col>
</template>

<script>
export default {
    props:['windowSize', 'button_size', 'lg_xl_ok', 'windowLgSize'],
    data:() => ({
        img_classes : [
        { id : "card1", img : require('../../images/logo.png')},
        { id : "card2", img : require('../../images/logo.png')},
        { id : "card3", img : require('../../images/logo.png')},
        { id : "card4", img : require('../../images/logo.png')},
        ],
        img_classes_xs_sm1 : [
            { id : "card1", img : require('../../images/logo.png')},
            { id : "card2", img : require('../../images/logo.png')},
        ],
        img_classes_xs_sm2 : [
            { id : "card3", img : require('../../images/logo.png')},
            { id : "card4", img : require('../../images/logo.png')},
        ],
        current_page : {
            'card1' : 1,
            'card2' : 2,
            'card3' : 3,
        }
    }),
    methods:{
        //홈의 맨 위의 버튼들을 누르면 해당 좌표로 스크롤이동
        goFocus(event){
            const id = event.target.id;

            if(id === "card4"){ //맨 오른쪽버튼은 소개페이지로 이동
                this.$router.replace('/about')
                return false;
            }

            if(this.lg_xl_ok){ //lg, xl 크기일 경우
                // this.$refs["button"][this.current_page[id]]._events.change[0]()
                this.$emit("child", this.current_page[id]);
                return false;
            }

            if(this.$vuetify.breakpoint.xs || this.$vuetify.breakpoint.sm){ //모바일 크기일 경우
                window.scrollTo({top: document.querySelector("." + id + "_top").offsetTop , behavior:'smooth'}) 
                return false;
            }

            document.querySelector("." + id + "_top").scrollIntoView({behavior:'smooth'}); //pc 크기일 경우
        },

        test(){
        }
       
    },
    computed : {
        xs_ok(){ 
            return this.$vuetify.breakpoint.xs ? true : false ;
        },
    }
}
</script>